(function ($) {
    initSlider();
    let menuHolder = $('.slimmenu'),
        jsSiteLogo = $('.js-site-logo'),
        clonedSiteLogo = jsSiteLogo.clone().addClass('cloned'),
        menuLengthMiddle = Math.ceil(menuHolder.children("li").length / 2),
        siteLogoHeight = jsSiteLogo.innerHeight() / 2 + 16;

    menuHolder.slimmenu({
        resizeWidth: '1023',
        collapserTitle: '',
        animSpeed: 'medium'
    });

    menuHolder.children('li:nth-child(' + menuLengthMiddle + ')').addClass('middle-item')
        .after(clonedSiteLogo).end()
        .children('li:nth-child(' + menuLengthMiddle + ')')
        .next("figure").css({'margin-bottom': -siteLogoHeight}).wrap('<li class="js-site-logo-cloned"></li>');

})(jQuery);

function getTimeRemaining(endTime) {
    let t = Date.parse(endTime) - Date.parse(new Date());
    let seconds = Math.floor((t / 1000) % 60);
    let minutes = Math.floor((t / 1000 / 60) % 60);
    let hours = Math.floor((t / (1000 * 60 * 60)) % 24);
    let days = Math.floor(t / (1000 * 60 * 60 * 24));
    return {
        'total': t,
        'days': days,
        'hours': hours,
        'minutes': minutes,
        'seconds': seconds
    };
}

function initializeClock(id, endTime) {
    const clock = document.getElementById(id);
    const daysSpan = clock.querySelector('.section-timer__days');
    const hoursSpan = clock.querySelector('.section-timer__hours');
    const minutesSpan = clock.querySelector('.section-timer__minutes');
    const secondsSpan = clock.querySelector('.section-timer__seconds');

    function updateClock() {
        let t = getTimeRemaining(endTime);

        daysSpan.innerHTML = t.days+'<small>Days</small>';
        hoursSpan.innerHTML = ('0' + t.hours).slice(-2)+'<small>Hrs</small>';
        minutesSpan.innerHTML = ('0' + t.minutes).slice(-2)+'<small>Min</small>';
        secondsSpan.innerHTML = ('0' + t.seconds).slice(-2)+'<small>Sec</small>';

        if (t.total <= 0) {
            clearInterval(timeInterval);
        }
    }

    updateClock();
    let timeInterval = setInterval(updateClock, 1000);
}

let deadline = document.getElementById('deadline').dataset.enddate;
initializeClock('deadline', deadline);